#are-main{width: 100%; height: 100vh; overflow: hidden;}
    .are-container{display: flex; width: 100%; height: 100%;} 
    .are-parent-left{width: 98px; border-right: 1px solid #111;}
    .are-parent-right{width: 100%;}
    .heading h2{line-height: 100%; font-family: 'Tungsten Narrow'; font-style: normal; font-weight: 600; font-size: 65px; text-transform: uppercase;}
    .are-sidenav-con{display: flex; flex-direction: column;padding: 40px 20px; justify-content: flex-start;align-items: center; height: 100%; position: relative;}
    .are-sidenav-btn{position: relative; width: 45px; height: 45px;}
    .are-sidenav-menu{height: 100%;}
    .are-sidenav-menu ul{align-content: center; display: flex; flex-direction: column; justify-content: center; height: 100%;}
    .are-sidenav-menu ul li{margin: 10px 0; padding: 10px; border-radius: 10px;}
    .are-sidenav-menu ul li a img{filter: brightness(0) invert(1); transition: fitler .6s ease-in-out;}
    .are-sidenav-menu ul li:hover img{filter: unset;}
    .are-sidenav-menu ul li.active-page-adm, .are-sidenav-menu ul li:hover{background: rgba(19, 92, 253, 0.1);}
    .are-sidenav-menu ul li.active-page-adm a img{filter: unset;}
    

    .are-sidenav-btn .page_nav_button-opened{margin-right: 0 !important;}
    .are-sidenav-btn .close-line-opened, .are-sidenav-btn .page_nav_button .close-line{height: 38px;}
     .are-left-message::-webkit-scrollbar{ width: 10px; }
     .are-left-message::-webkit-scrollbar-track{background:  rgba(0,0,0,0.2); }
     .are-left-message::-webkit-scrollbar-thumb{ border-radius: 10px; background: #333; opacity: 0.5; cursor: grab; } 
     .are-left-message::-webkit-scrollbar-thumb:hover{ cursor: grab; }
     .are-left-message { scrollbar-width: 10px; }
     .are-left-message{ scrollbar-color: #333 rgba(0,0,0,0.5); scrollbar-width: thin; }
     .are-msg-section.isRead{position: relative;}
     .are-msg-section.isRead::before{content: ''; position: absolute; left: 0; border-radius: 50%; width: 10px; height: 10px; background: #135CFD; top: 50%; transform: translateY(-50%);}

    .are-parentright-con{width: 100%; height: 100%;}
    .are-left-message{width: 25%; overflow-x: hidden; max-height: 81vh;overflow-y: scroll;padding: 40px 0; border-bottom: 1px solid #111;}
    .are-left-message section{display: flex; border-radius: 10px;margin: 0 10px 0; padding: 10px 10px;} 
    .are-left-message section:hover{cursor: pointer;}
    .are-left-message section.active-section{background: rgba(19, 92, 253, 0.1);}
    .are-left-message section figure{margin-right: 10px;}
    .are-left-message section h2{font-weight: bold; font-size: 18px;}
    .are-left-message section p{font-size: 16px; overflow: hidden; position: relative; text-overflow: ellipsis; white-space: nowrap;   display: inline-block;   max-width: 200px; overflow-x: hidden;}
    .are-left-message section span{  position: absolute; font-size: 11px; width: 100%; top: 0px; right: -183px;}
    .are-left-message-height{}
    .are-msg-info-text{position: relative;}
    .are-parent-message{display: flex; height: 100%; width: 100%; }
    .are-right-message{width: 100%; padding: 40px 25px; position: relative; max-height: 81vh; border-bottom: 1px solid #111;}
    
    .are-main-top{padding: 30px 25px; border-bottom: 1px solid #111;}
    .are-rightmsg-section{position: absolute; opacity: 0;}
    .are-rightmsg-section.active-section{opacity: 1; }


    .are-right-message h2{line-height: 100%; border-bottom: 1px solid #333; padding-bottom: 25px; margin-bottom: 25px; font-family: 'Tungsten Narrow'; font-style: normal; font-weight: 600; font-size: 45px; text-transform: uppercase;}
    .are-right-message p{display: flex;font-weight: 500; font-size: 18px; margin-bottom: 20px; justify-content: space-between; text-align: left;}
    .are-right-message p span{width: 50%; font-weight: normal !important; font-size: 17px !important;}
    .are-message-span{display: block !important; }
    .are-message-span span{display: block; width: 100% !important;}






