.loader-animate{background: #070707;  animation: loaderSlideHome 3s cubic-bezier(0.075, 0.82, 0.165, 1) ;   animation-delay: 2400ms; position: fixed; overflow: hidden; transition: 0.6s cubic-bezier(0.075, 0.82, 0.165, 1); top: 0; left: 0; width: 100%; height: 100vh; z-index: 999; transition-delay: 0s; opacity: 1;  }
.loader-animate .loader-info{display: flex;  animation: circleMobile 3s  cubic-bezier(0.075, 0.82, 0.165, 1) ;  transform: translateY(0);  transform: translateY(0);     transition: 0.6s cubic-bezier(0.075, 0.82, 0.165, 1); align-items: center; width: 100%;  height: 100%;text-align: center; background: #333; position: relative; }
.loader-animate .loader-info h2{width: 100%; font-size: 6vw; line-height: 100%;  font-family: 'Tungsten Narrow'; font-style: normal; font-weight: 600;}
.loader-animate .loader-info h2 span{display: block; }
.loader-animate.loader-show{visibility: visible; top: 0; z-index: 999; visibility: visible; transition-delay: 0s;}
.loader-info.loader-load{animation: circle 3s  cubic-bezier(0.075, 0.82, 0.165, 1); }

#nh-loader{visibility: hidden;}
.visible{visibility: visible !important;}
.nonhome-loader{background: #070707; visibility: visible; animation: loaderSlide 3s cubic-bezier(0.075, 0.82, 0.165, 1); animation-delay: 500ms; position: fixed; overflow: hidden; transition: 0.6s cubic-bezier(0.075, 0.82, 0.165, 1); bottom: -79%; left: 0; width: 100%; height: 100vh;  z-index: 999; visibility: visible; transition-delay: 0s;  }
.nonhome-loader.loader-info{display: flex;  transition: 0.6s cubic-bezier(0.075, 0.82, 0.165, 1); align-items: center; width: 100%;  height: 100%;text-align: center; background: #070707; position: relative; }
.nonhome-loader.loader-info h2{width: 100%; font-size: 7vw; line-height: 100%;  font-family: 'Tungsten Narrow'; font-style: normal; font-weight: 600;}
.nonhome-loader.loader-info h2 span{display: block; color: #135CFD;}
.nonhome-loader.loader-show{visibility: visible; top: 0; z-index: 999; visibility: visible; transition-delay: 0s;}
.loader-info.loader-load{animation: circle 2s  cubic-bezier(0.075, 0.82, 0.165, 1); }

@keyframes circleMobile{
  0% { transform: translateY(0); clip-path: circle(100%);  background: #333 !important;}
  25%{clip-path: circle(75%);  background: #333 !important;}
  50% {clip-path: circle(50%);  background: #333 !important;}
  75%{ transform: translateY(0); clip-path: circle(25%); background: #333 !important;}
  100% {  transform: translateY(-108%);   clip-path: circle(20%); }
}
@keyframes loaderSlideHome {
  0%{ top: 0 !important;  visibility: visible !important; opacity: 1;}
   100%{ top: -107%;  visibility: visible !important;  opacity: 1;}
}
@keyframes loaderSlide {
  0%{ bottom: -79%; }
   100%{ bottom: 107%; }
}
    




